/**************
EPISODE SELECTION
**************/

.cards {

    width: 175%;
    height: 100%;
    color: white;
    text-align: center;
    text-shadow: 0px 1px 2px #1B1B1B;

    .card{
        position: relative;
        overflow: hidden;
        width: 14.28%;
        height: 100%;
        float: left;
        transition: all .3s;
    }
}

.select .content {
    width: 100%;
    position: absolute;
    top: 33%;
    left: 50%;
    transform: translate(-50%, -50%);

    .number{
        display: inline-block;
        margin-bottom: 20px;
        width: 60px;
        height: 60px;
        position: relative;
        transform: rotate(45deg);
        border: 2px solid white;
        border-radius: 20%;
        box-shadow: 0px 1px 2px #1B1B1B;
        transition: all .3s;

        p{
            text-shadow: 0px 0px 2px #1B1B1B;
            transform:  rotate(-45deg) translate(-11px, 11px);
            font-size: 30px;
            padding: auto;
            top: 50%;
            left: 50%;
        }
    }
}
.card{
    span.background {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        transition: transform .3s;
    }
    &:hover {
        .darken {
            opacity: 0;
        }
        .number {
            background : white;
            color: transparent;
            p{
                color: black;
                text-shadow: 0px 0px 0px white;
            }
        }
        span.background{
            transform: scale(1.02);
        }
    }


    .darken{
        width: 100%;
        height: 100%;
        background-color: #1C1C1C;
        opacity: .15;
        transition: opacity .3s;
    }
    .lock {
        position: absolute;
        top:50%; left: 50%;
        width: 50px;
        transform: translate(-50%, -50%);
        opacity: .95;

    }
    &:hover .lock {
        animation: pop .5s;

    }
}
.card:nth-child(1){
    span.background {
        background-image: url('../img/film-1.jpg')
    }
}
.card:nth-child(2){
    span.background {
        background-image: url('../img/film-2.jpg')
    }
}
.card:nth-child(3) {
    span.background {
        background-image: url(../img/film-3.jpg);
    }
    .darken {
        opacity: 0.65;
    }
}
.card:nth-child(4) {
